import apis from '@/api/api.js' // 导入我们封装好的apis对象
import axios from '@/api/axios.js' // 导入我们封装好的axios对象

export const getCamelByVo = params => { // 从外部接受参数，没有参数默认为空对象
    console.log(apis,params);
    return axios.post(apis.getCamel, params).then(res=>res.info); // return对应的get/post方法，第一个填路径，第二个给参数对象
}

export const updateAcceptablePrice = params => { // 从外部接受参数，没有参数默认为空对象
    return axios.post(apis.updateAcceptablePrice, params); // return对应的get/post方法，第一个填路径，第二个给参数对象
}




