<template>
<div>

  <query-form @putInstance="getInstance"></query-form>
  <el-table
    :data="tableData"
    border
    style="width: 100%">

    <el-table-column
      fixed
      prop="asin"
      label="主键"
      width="150">
    </el-table-column>

    <el-table-column
      prop="title"
      label="标题"
      width="120">
    </el-table-column>

    <el-table-column
      prop="state"
      label="国家"
      width="120">
    </el-table-column>

    <el-table-column
      prop="currentPrice"
      label="现在价格"
      width="120">
    </el-table-column>

    <el-table-column
      prop="highestPrice"
      label="最高价格"
      width="300">
    </el-table-column>

    <el-table-column
      prop="lowestPrice"
      label="最低价格"
      width="120">
    </el-table-column>

    <el-table-column
      prop="updateTime"
      label="更新时间"
      :formatter="dateFormat"
      width="120">
    </el-table-column>

    <el-table-column
      prop="acceptablePrice"
      label="接受价格"
      width="300">
    </el-table-column>

    <el-table-column
    fixed="right"
    label="操作"
    width="100">
    <template slot-scope="scope">
      <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
      <el-button type="text" size="small">编辑</el-button>
    </template>
    </el-table-column>


  </el-table>
  <el-pagination
    background
    layout="total, sizes, prev, pager, next, jumper"
    :total="total"
    :current-page="currentPage"
    :page-sizes="[5, 7, 10]"
    :page-size="pageSize"
    @size-change = "refresh"
    @current-change = "changePage"
    @prev-click = "changePage"
    @next-click = "changePage">
  </el-pagination>

  <edit-camel ref="editCamel" ></edit-camel>

</div>

</template>


<script>

import {getCamelByVo} from '@/api/index.js'
import editCamel from '@/components/manage/EditCamel.vue'
import queryForm from '@/components/manage/QueryForm.vue'

  export default {
    components: {editCamel,queryForm} ,
    data(){
        return {
            //当前数据
            tableData: [],

            //默认每页数据量
            pageSize: 5,

            //当前页码
            currentPage: 1,

            //默认数据总数
            total: 1000,
            
            //查询视图
            vo:{},

            //查询对象
            instance:{}
        }
        
      },
      methods: {

          //对话框展示
          handleClick(row) {
            this.row = row;
            this.$refs.editCamel.showModel(row);
          },

          //日期格式化
          dateFormat(row, column, cellValue, index){
            return new Date(cellValue).toLocaleDateString();
          },

          //根据每页数量刷新
          refresh(pageSize){
            this.pageSize = pageSize;
            this.toPage();
          },

          //改变页数
          changePage(pageNum){
            this.currentPage = pageNum;
            this.toPage();
          },

          //通用页面跳转
          toPage(){
            var _this = this;
            this.createParams();
            getCamelByVo(this.vo).then(res=> {
              _this.tableData = res.list;
              _this.currentPage = res.pageNum;
              _this.total = res.total;
            })
          },

          //查询instance传递
          getInstance(instance){
            this.currentPage = 1;
            this.instance = instance;
            this.toPage();
          },

          //创建参数
          createParams(){
            this.vo = {
              page: this.currentPage,
              limit:this.pageSize,
              instance:this.instance
            }
          }
      },

      //初始化页面跳转
      mounted:function(){
          this.toPage();
      },
    

  }
</script>



