<template>
    <el-form :inline="true" :model="formInline" ref="queryForm" class="demo-form-inline">
        <el-form-item label="ASIN">
            <el-input v-model="formInline.asin" placeholder="asin"></el-input>
        </el-form-item>

        <el-form-item label="重点监控" prop="important">
            <el-select v-model="formInline.important" placeholder="请选择重点监控">
                <el-option v-for="option in this.$store.state.options"
                    :key="option.value"
                    :label="option.label"
                    :value="option.value"
                ></el-option>
            </el-select>
        </el-form-item>

        <el-form-item>
            <el-button type="primary" @click="onSubmit">查询</el-button>
            <el-button @click="resetForm('queryForm')">重置</el-button>
        </el-form-item>
    </el-form>
</template>
<script>

  export default {
    data() {
      return {
        formInline: {
          asin: '',
          important:null
        }
      }
    },
    methods: {
      onSubmit() {
        this.$emit("putInstance",this.formInline);
      },
      resetForm(formName){
        this.$refs[formName].resetFields();
      }
    },

  }
</script>


