import Vue from 'vue'
import Router from 'vue-router'
import BlogLogin from '@/components/manage/BlogLogin.vue'
import BlogIndex from '@/components/home/BlogIndex.vue'
import HelloWorld from '@/components/home/HelloWorld.vue'
import UploadFile from '@/components/manage/UploadFile.vue'
import Camel from '@/components/manage/CamelShow.vue'
import editCamel from '@/components/manage/EditCamel.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/edit'
    },
    {
      path: '/edit',
      name: 'edit',
      component: editCamel
    },
    {
      path: '/camel',
      name: 'Camel',
      component: Camel
    },
    {
      path: '/index',
      name: 'BlogIndex',
      component: BlogIndex
    },
    {
      path: '/manage',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'BlogLogin',
      component: BlogLogin
    },
    {
      path: '/hello',
      name: 'hello',
      component: HelloWorld
    },
    {
      path: '/upload',
      name: 'upload',
      component: UploadFile
    }
  ]
})



