<template>
<div>
<el-dialog
  title="提示"
  :visible.sync="dialogVisible"
  width="30%"
  :before-close="handleClose">

    <el-radio-group v-model="labelPosition" size="small">
        <el-radio-button label="left">左对齐</el-radio-button>
        <el-radio-button label="right">右对齐</el-radio-button>
        <el-radio-button label="top">顶部对齐</el-radio-button>
    </el-radio-group>

    <div style="margin: 20px;"></div>

    <el-form :label-position="labelPosition" label-width="100px" :model="data" :rules="rules" ref="itemForm">
        <el-form-item label="主要标识">
            <el-input v-model="data.asin" disabled></el-input>
        </el-form-item>

        <el-form-item label="商品名称">
            <el-input v-model="data.title" disabled></el-input>
        </el-form-item>

        <el-form-item label="最高价格">
            <el-input v-model="data.highestPrice" disabled></el-input>
        </el-form-item>
        <el-form-item label="最低价格">
            <el-input v-model="data.lowestPrice" disabled></el-input>
        </el-form-item>
        <el-form-item label="现在价格">
            <el-input v-model="data.currentPrice" disabled></el-input>
        </el-form-item>

        <el-form-item label="重点监控" prop="important">
            <el-select v-model="data.important" placeholder="请选择重点监控">
                <el-option v-for="option in this.$store.state.options"
                    :key="option.value"
                    :label="option.label"
                    :value="option.value"
                ></el-option>
            </el-select>
        </el-form-item>

        <el-form-item label="接受价格" prop="acceptablePrice">
            <el-input v-model="data.acceptablePrice"></el-input>
        </el-form-item>

        <el-form-item>
            <el-button type="primary" @click="submitForm('itemForm')">立即创建</el-button>
            <el-button @click="resetForm('itemForm')">重置</el-button>
        </el-form-item>

    </el-form>
</el-dialog>

</div>
</template>

<script>

import {updateAcceptablePrice} from '@/api/index.js';

export default {

    //props:{row:Object},
    data(){
        //自定义校验规则
        var validateImportant = (rule, value, callback) => {
            if (value != "1") {
                callback(new Error('必须成为重点监控商品'));
            } else {
                callback();
            }
        };

        return {
            //form 展示
            dialogVisible:false,     //模态框是否显示
            //显示加载
            addLoading: true,       //是否显示loading
            //对齐选项
            labelPosition: 'right',
            //form数据
            data: {},
            options:[
                {label:"是",value:1},
                {label:"否",value:0}
            ],
            //校验规则
            rules: {
                acceptablePrice: [
                    { required: true, message: '必须填写接收价格', trigger: 'blur' },
                    { min: 1, max: 8, message: '长度在 1 到 8 个字符', trigger: 'blur' }
                ],
                important : [
                    { required: true, message: '必须选择重点监控', trigger: 'blur' },
                    { validator: validateImportant, trigger: 'blur' }
                ]
            }
        }
    },
    methods:{
        //展示对话框
        showModel(row){
            this.data = row;
            this.dialogVisible = true;
        },
        //关闭前
        handleClose(done) {
            this.$confirm('确认关闭？')
            .then(_ => {
                //清除校验结果
                this.resetForm('itemForm');
                //执行关闭
                done();
            })
            .catch(_ => {});
        },
        //对话框提交
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.executeUpdate();
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        //执行更新
        executeUpdate(){
            updateAcceptablePrice(this.data).then(res=>{
                this.alert('恭喜你，这是一条成功消息','success');
                this.resetForm('itemForm');
                this.dialogVisible = false;
            })
        },
        //消息提示
        alert(msg,type){
            this.$message({
                message: msg,
                type: type
            });
        },
        //重置form
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
    }
}
</script>

<style>

</style>


