// 如果在模块化构建系统中，请确保在开头调用了 Vue.use(Vuex)
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const state = {
    options:[
        {label:"是",value:1},
        {label:"否",value:0}
    ]
}

export default new Vuex.Store({
    state
})



